@-webkit-keyframes searchExpand {
  0% {
    width: 0;
  }

  100% {
    width: 80%;
    border: 1px black solid;
  }
}

@keyframes searchExpand {
  0% {
    width: 0;
  }

  100% {
    width: 80%;
    border: 1px black solid;
  }
}

@-webkit-keyframes searchCollapse {
  0% {
    width: 80%;
    border: 1px black solid;
  }

  100% {
    width: 0;
  }
}

@keyframes searchCollapse {
  0% {
    width: 80%;
    border: 1px black solid;
  }

  100% {
    width: 0;
  }
}

.post-title {
  text-decoration: none;
  color: #FFFFFF;
}

.post-title h1 {
  color: rgba(27,27,30,1);
  margin: 2.5px 0;
}

.post-content p {
  margin: 0;
}

.post-search {
  text-align: center;
}

.search-container {
  max-width: 300px;
  margin: 0 auto;
  text-align: center;
  margin-bottom: 15px;
}

.search-expand {
  -webkit-animation: searchExpand 1s forwards;
          animation: searchExpand 1s forwards;
}

.search-collapse {
  -webkit-animation: searchCollapse 1s forwards;
          animation: searchCollapse 1s forwards;
}

.search-container input {
  width: 0;
  border: none;
  display: inline-block;
}

.search-container i {
  cursor: pointer;
  display: inline-block;
  margin-right: 10px;
  font-size: 1.5rem;
}

.post-preview {
  /*border: 1px solid rgba(27,27,30,0.7);*/
  /*border-radius: 5px;*/
  padding: 2px 10px;
  margin-bottom: 25px;
  text-decoration: none;
  text-align: center;
}

.post-preview:hover {
  background-color: rgba(27,27,30,0.1);
}

hr.pretty {
  border: 0;
  height: 1px;
  background-image: -webkit-gradient(linear, left top, right top, from(rgba(0, 0, 0, 0)), color-stop(rgba(0, 0, 0, 0.75)), to(rgba(0, 0, 0, 0)));
  background-image: -webkit-linear-gradient(left, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0));
  background-image: -o-linear-gradient(left, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0));
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0));
}



