@import url('https://fonts.googleapis.com/css?family=Audiowide|Julius+Sans+One|Knewave|Patrick+Hand+SC|Press+Start+2P|VT323');

body {
  background-color: #FFFFFF;
  color: #1B1B1E;
}

h1,h2,h3,h4,h5,h6 {
  color: #1B1B1E;
  font-family: 'Audiowide', cursive;
}

p,a,div,span,nav,aside,section,button {
  font-size: 1rem;
  color: #1B1B1E;
  font-family: 'Alegreya', serif;
}

h1#main-header {
  font-size: 2.75rem;
  text-align: center;
  margin-bottom: 10px;
  color: #1B1B1E;
}

div.post-list h1 {
  font-size: 2rem;
}



