#navbar {
  margin: 0 10px 20px 10px;
  padding: 10px 5px;
  clear: both;
}

#navbar #navbar-left {
  float: left;
}

#navbar #navbar-right {
  float: right;
}

#navbar .nav-link {
  margin: 0 10px;
  background-color: #373F51;
  color: #FFFFFF;
  padding: 5px 10px;
  text-decoration: none;
}

#navbar .nav-link:hover {
  background-color: #58A4B0;
}

#navbar .nav-link.active {
  background-color: #58A4B0;
}

#navbar .nav-btn {
  border: none;
  background-color: #58A4B0;
  cursor: pointer;
  padding: 5px 10px;
  opacity: 0.75;
}

#navbar .nav-btn:hover {
  opacity: 1;
}



